import React from "react";
import "../styles/header.css";

const Header = ({ user }) => {
  // Si donnes par encore charges (nul, undefined)
  if (!user || !user.userInfos) return <header>Chargement...</header>;
  return (
    <header>
      <h1>Bonjour <span className="user-name">{user.userInfos.firstName}</span></h1>
      <p>Flicitation ! Vous avez explos vos objectifs hier </p>
    </header>
  );
};
export default Header;
