import React from "react";
import { // import de plusieurs composants de la librairie Recharts, utilise pour crer les graphiques
  BarChart,
  Bar, // reprsente une srie de donnes sous forme de barre
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer, // conteneur qui rend le graphique adaptatif  la taille de son parent
} from "recharts";
import CustomTooltipActivity from "./CustomTooltipActivity"; // importe un tooltip personnalis
import "../styles/activityGraph.css";

function ActivityGraph({ data }) {
  if (!data) return null; // si data est null ou undefined, le composant ne retourne rien = viter les erreurs

  // Lgende personnalise avec ronds
  const renderLegend = () => (
    <div
      style={{
        display: "flex",
        gap: "20px",
        justifyContent: "flex-end",
        marginBottom: "10px",
      }}
    >
      {/* lgende pour le Poids */}
      <div style={{ display: "flex", alignItems: "center", gap: "5px" }}>
        <div
          style={{
            width: "10px",
            height: "10px",
            borderRadius: "50%",
            backgroundColor: "#282D30",
          }}
        ></div>
        <span style={{ fontSize: "14px", color: "#74798C" }}>Poids (kg)</span>
      </div>
      {/* lgende pour les Calories brles */}
      <div style={{ display: "flex", alignItems: "center", gap: "5px" }}>
        <div
          style={{
            width: "10px",
            height: "10px",
            borderRadius: "50%",
            backgroundColor: "#E60000",
          }}
        ></div>
        <span style={{ fontSize: "14px", color: "#74798C" }}>
          Calories brles (kCal)
        </span>
      </div>
    </div>
  );

  return (
    // Conteneur principal du graphique
    <div className="activity-graph">
      <div>
        <h3>Activit quotidienne</h3>
      </div>
      {/* Rend le graphique adaptatif  la taille de son parent (width et height  100%) */}
      <ResponsiveContainer width="100%" height="100%">
        <BarChart
          data={data}
          barGap={8} // espace entre les barres
          margin={{ top: 30, right: 30, left: 30, bottom: 10 }} // marge interne autour du graphique pour le dgagement
        >
          {/* Quadrillage horizontal uniquement (vertical={false}) avec des traits en pointills (3 3) */}
          <CartesianGrid strokeDasharray="3 3" vertical={false}/>
          {/* Axe horizontal / tickLine={false} et axisLine={false} : supprime les traits du tick et de laxe */}
          <XAxis dataKey="day" tickLine={false} axisLine={false}/> 
          {/* Axe vertical pour le Poids */}
          <YAxis
            yAxisId="kg"
            orientation="right" // axe  droite du graphique
            tickLine={false}
            axisLine={false}
            domain={["dataMin - 1", "dataMax + 1"]} // ajustement automatique pour que les barres ne touchent pas les bords
            allowDecimals={false}
          />
          {/* On cache l'axe vertical pour les Calories brles */}
          <YAxis yAxisId="cal" hide />
          {/* Bulles : affiche un tooltip personnalis en utilisant composant CustomTooltipActivity */}
          <Tooltip content={<CustomTooltipActivity />} />
          {/* Lgende personnalise dfinie dans renderLegend*/}
          <Legend content={renderLegend} verticalAlign="top" align="right" />
          {/* les barres du poids */}
          <Bar
            yAxisId="kg"
            dataKey="kilogram"
            fill="#282D30"
            radius={[10, 10, 0, 0]}
            barSize={7}
            name="Poids (kg)"
          />
          {/* barres des calories */}
          <Bar
            yAxisId="cal"
            dataKey="calories"
            fill="#E60000"
            radius={[10, 10, 0, 0]}
            barSize={7}
            name="Calories brles (kCal)"
          />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
}
export default ActivityGraph;
