import React from "react";
import {
  RadialBarChart,
  RadialBar, // barre circulaire reprsentant une valeur
  ResponsiveContainer
} from "recharts";
import "../styles/userScore.css";

// Remarqur : ici il faut DEUX barres empiles ( 1 pour le score rel en rouge et une 2me pour le reste jusqu' 100 en transparent). Recharts ne gre pas directement un "cercle" de progression
function UserScore({ user }) {
  if (!user) return null; // si lutilisateur nest pas encore charg : on affiche rien

  const score = (user.todayScore || user.score) * 100; // Attention au pige dans data.js !! / on multiplie par 100 car le graphique attend une valeur entre 0 et 100 !!
  const data = [ // tableau de donnes pour Recharts (obligatoire)
    {
      name: "score",
      value: score,
      fill: "#FF0000",
    },
    {
      name: "rest",
      value: 100 - score,
      fill: "transparent",
    },
  ];

  return (
    // Conteneur principal du composant
    <div className="user-score">
      <h3>Score</h3>
      <ResponsiveContainer width="100%" height={250}>
        <RadialBarChart
          innerRadius="70%" // on cre un anneau (pas un disque plein)
          outerRadius="85%" // paisseur de lanneau
          startAngle={90} // faire un tour complet dans le sens horaire
          endAngle={450}
          data={data} // les 2 barres empiles
        >
          <RadialBar
            dataKey="value"
            cornerRadius={10}
            stackId="score"
            barSize={12}
          />
        </RadialBarChart>
      </ResponsiveContainer>
      <div className="score-text">
        <strong>{score}%</strong>
        <span>de votre<br />objectif</span>
      </div>
    </div>
  );
}
export default UserScore;
