import React from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Rectangle, // utilis pour crer un curseur personnalis au survol
} from "recharts";
import "../styles/averageSessionsGraph.css";

const AverageSessionsGraph = ({ data }) => {
  if (!data || data.length === 0) return null; // Si data nexiste pas ou est vide = on naffiche rien. vite les erreurs de rendu
  const days = ["L", "M", "M", "J", "V", "S", "D"];

  // Recharts attend des donnes formates
  const chartData = data.map((item) => ({
    day: days[item.day - 1],
    value: item.sessionLength, // dure de la session.
  }));

  // on dclare un composant interne pour le tooltip
  const CustomTooltip = ({ active, payload }) => { // active : true quand on survole un point / payload : contient la valeur de la donne survole
    if (active && payload && payload.length) { // vrification tooltip est actif + des donnes existent
      return (
        <div className="sessions-tooltip">
          {payload[0].value} min
        </div>
      );
    }
    return null; // si pas de survol = on affiche rien
  };

  // Curseur sombre au survol (2e nuance de rouge)
  const CustomCursor = ({ points, width, height }) => {
    const { x } = points[0]; // position horizontale du point survol
    return ( // on rcupre la coordonne X du point actif 
      <Rectangle // on dessine un rectangle sombre semi-transparent
        fill="rgba(0, 0, 0, 0.1)"
        x={x}
        y={0}
        width={width}
        height={height}
      />
    );
  };

  return (
    // Conteneur principal du graphique
    <div className="average-sessions-graph">
      {/* Titre DANS le graph */}
      <h3 className="sessions-title">Dure moyenne des sessions</h3>
      {/* Permet au graphique de sadapter automatiquement  son parent */}
      <ResponsiveContainer width="100%" height="100%">
        <LineChart
          data={chartData} // donnes formates
          margin={{ top: 60, right: 20, left: 20, bottom: 20 }}
        >
          {/* Axe Y cach mais obligatoire */}
          <YAxis hide domain={["dataMin - 10", "dataMax + 10"]} />
          {/* Axe X jours */}
          <XAxis
            dataKey="day"
            axisLine={false}
            tickLine={false}
            stroke="rgba(255,255,255,0.6)" // Texte blanc lgrement transparent
            tick={{ fontSize: 12 }}
          />
          <Tooltip
            content={<CustomTooltip />} // tooltip personnalis
            cursor={<CustomCursor />} // curseur sombre au survol.
          />
          {/* Courbe */}
          <Line
            type="monotone" // courbe fluide
            dataKey="value"
            stroke="#FFFFFF"
            strokeWidth={2}
            dot={false}
            activeDot={{ r: 4, strokeWidth: 0 }} // Petit point uniquement au survol
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};
export default AverageSessionsGraph;
