import "../styles/activityGraph.css"

// props envoyes automatiquement par Recharts
const CustomTooltipActivity = ({ active, payload }) => { // active : si true on survol une barre + payload : tableau contenant les donnes de la barre survole, chaque entre correspond  une srie (Bar) du graphique
  if (!active || !payload || payload.length < 2) return null; // si : tooltip nest pas actif, ou payload nexiste pas, ou il y a moins de 2 valeurs = on affiche rien. Avec .length on veut tre sr que les deux valeurs (DES DEUX BARRES DONC !!) sont disponibles avant dafficher le tooltip
  return (
    <div className="activity-tooltip">
      <p>{payload[0].value}kg</p>
      <p>{payload[1].value}kCal</p>
    </div>
  );
};
export default CustomTooltipActivity;
